.form-message {
  position: absolute;
  left: 0;
  top: -50px;
  
  background: lightsalmon;
  border-radius: $brd-radius $brd-radius 0 0;
  display: none;
  padding: 5px 10px;
  width: 100%;
}


.form-message.ang-show-message {
  display: block;
  animation: show-message 5s linear backwards;
}


// Form message animation
@keyframes show-message {
  0% {
    top: -50px;
  }
  
  10% {
    top: 0;
  }
  
  90% {
    top: 0;
  }

  100% {
    top: -50px; 
  }
}